<script>
import {
  getData,
  tableResize //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
export default {
  name: "reportAftersalesdetrpt",
  data() {
    return {
      authority: {},
      params: {},
      searchParams: {},
      listShow: false,
      timeValue: null,
      exportType: null,
      TableData: {
        allList: {},
        tableData: []
      },
      SelesTomer: {
        options: []
      }, //客户名称
      departmentList: {
        options: []
      },
      companyList: [],
      goPage: 1, //默认搜索页面
      pageSize: 30,
      per_page: [30, 50, 100, 500],
      loading: false,
      flag: true,
      tableHeight: 600
    };
  },

  methods: {
    // 公司理赔和厂家理赔切换
    payChnage(val) {
      // val 0 厂家理赔单起止日期 1 公司理赔单起止日期
      let start_time = "company_start_time",
        end_time = "company_end_time";
      if (val == 0) {
        start_time = "manufactor_start_time";
        end_time = "manufactor_end_time";
        this.params.company_start_time && delete this.params.company_start_time;
        this.params.company_end_time && delete this.params.company_end_time;
      } else if (val == 1) {
        start_time = "company_start_time";
        end_time = "company_end_time";
        this.params.manufactor_start_time &&
          delete this.params.manufactor_start_time;
        this.params.manufactor_end_time &&
          delete this.params.manufactor_end_time;
      }
      if (this.timeValue) {
        this.params[start_time] = this.timeValue[0];
        this.params[end_time] = this.timeValue[1];
      } else {
        this.params[start_time] = "";
        this.params[end_time] = "";
      }
    },
    change(val) {
      if (val) {
        //选择的日期
        if (this.params.type == 1) {
          this.params.start_time = this.params.company_start_time = val[0];
          this.params.end_time = this.params.company_end_time = val[1];
        } else {
          this.params.start_time = this.params.manufactor_start_time = val[0];
          this.params.end_time = this.params.manufactor_end_time = val[1];
        }
      } else {
        let arrField = [
          "company_start_time",
          "company_end_time",
          "manufactor_start_time",
          "manufactor_end_time",
          "start_time",
          "end_time"
        ];
        arrField.forEach(key => {
          this.params[key] && delete this.params[key];
        });
      }
    },
    // 理赔判断
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        type: "1"
      });
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {
          type: "1"
        }, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "claimProfitList", //接口名
        vm: this //传递的this
      });
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });

      getData(this.searchParams, 2);
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      if (this.listShow == false) {
        this.$message({
          message: "请查询需要导出的数据",
          type: "error"
        });
        return false;
      }
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.exportClaimProfitListUrl(this.params).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    getSummaries(param) {
      let _this = this;
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 2) {
          sums[index] = "本页合计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);

          if (index === 7) {
            //公司磨损费
            sums[index] = sums[index]
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (index === 8) {
            //公司内垫费
            sums[index] = sums[index]
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (index === 14) {
            //厂家磨损费
            sums[index] = sums[index]
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (index === 15) {
            //厂家内垫费
            sums[index] = sums[index]
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (index === 19) {
            //公司理赔单价
            sums[index] = sums[index]
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (index === 20) {
            //厂家理赔单价
            sums[index] = sums[index]
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (index === 21) {
            //厂家理赔单价
            sums[index] = sums[index]
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
    onSearch() {
      //查询
      this.listShow = true;
      this.$set(this, "searchParams", {
        params: {}, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "claimProfitList", //接口名
        vm: this //传递的this
      });
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams, 2, tableResize.bind(this));
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "params", {
      type: "1"
    });
  },
  mounted() {
    const elTable1 = this.$refs.table1.$el.getElementsByClassName(
      "el-table__body-wrapper"
    )[0];
    const elTable2 = this.$refs.table_sum;
    elTable1.addEventListener("scroll", function() {
      elTable2.scrollLeft = this.scrollLeft;
    });
    elTable2.addEventListener("scroll", function() {
      elTable1.scrollLeft = this.scrollLeft;
    });
  }
};
</script>

